body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*{
  padding: 0;
  margin: 0;
}

body { font-family: sans-serif; height: 100%; width: 100%;}

.canvas{
  width: 100%;
  height: 100%;
  display: flex;
  max-width: 1024px;  
  margin: 0 auto;
  flex-wrap: wrap;  
}

.scene {
  width: calc(246px - 20px);
  height: calc(246px - 20px);
  margin: 10px;
  -webkit-perspective: 600px;
          perspective: 600px;  
}

.card {
  width: 100%;
  height: 100%;
  transition: -webkit-transform 1s;
  transition: transform 1s;
  transition: transform 1s, -webkit-transform 1s;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  cursor: pointer;
  position: relative;  
}

.card.is-flipped {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.card.matched{  
  visibility: hidden;
}

.card__face {
  position: absolute;
  width: 100%;
  height: 100%;
  line-height: 260px;
  color: white;
  text-align: center;
  font-weight: bold;
  font-size: 40px;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}

.card__face--front {
  background: #aa0044;  
}

.card__face--back {
  background: repeating-linear-gradient(45deg, #606dbc, #606dbc 10px, #465298 10px, #465298 20px), repeating-linear-gradient(
    310deg,
    black,
    black 10px,
    white 10px,
    white 20px
  );  
  -webkit-transform: rotateY(180deg);  
          transform: rotateY(180deg);
}

